import { Component, OnInit } from '@angular/core';

interface CONFIG_ITEM {
    name:string;
    title:string;
    items?:CONFIG_ITEM[];
    cpath?:string;          //config path
    template?:string;       //angular template
    ui_expanded?:boolean;   //only for this component
}

@Component({
    selector: 'store-payment',
    templateUrl: 'payment.component.html',
    styleUrls: ['payment.component.scss']
})

export class PaymentComponent implements OnInit {
    items:CONFIG_ITEM[];
    title:string;
    constructor() { }

    ngOnInit() { 
        this.title = 'Configuration'
        this.items = [
            {
                name:'general',
                title:'General',
                items:[
                    {
                        name:"general",
                        title:'General',
                        items:[]
                    },
                    {
                        name:"web",
                        title:'Web',
                        items:[]
                    },
                    {
                        name:"contacts",
                        title:'Contacts',
                        items:[]
                    }
                ]
            },
            {
                name:'sales',
                title:'Sales',
                items:[
                    {
                        name:"sales",
                        title:"Sales",
                        items:[]
                    },
                    {
                        name:"paymenet methods",
                        title:'Payment Methods',
                        items:[
                            {
                                name:"eWay_payment",
                                title:'eWay Rapid3.1',
                                items:[
                                    {
                                        name:"active",
                                        title:'Enable this solution',
                                        cpath:'payment/eway/active'
                                    }
                                ]
                            },
                        ]
                    }
                ]
            }
        ]
    }
}


